.BanarSection {
  background-color: var(--color3);
  padding-top: 120px;
  position: relative;
}

.BanarSection .btnBox .btn1{
  background-color: var(--color3);
}
.BanarSection .btnBox .btn1:hover{
  background-color: transparent;
}
.BanarSection::after {
  content: "";
  position: absolute;
  /* background: rgba(17, 17, 17, 0.68); */
  background: var(--color1);
  height: 419px;
  left: 0;
  right: 0;
  margin: 0 120px;
  top: 106px;
  z-index: 0;
}
.BanarSection .intro {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  z-index: 1;
}
.BanarSection .intro .intro2 .iam {
  background-color: var(--color3);
  width: 150px;
  height: 45px;
  border: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: var(--color2);
  position: relative;
  margin-bottom: 10px;
}
.BanarSection .intro .intro2 .iam::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  left: 45%;
  bottom: -20%;
  background-color: var(--color3);
  transform: rotate(45deg);
}
.BanarSection .intro2 h2 {
  font-weight: 700;
  font-size: 3.5rem;
  line-height: 61px;
  color: var(--color2);
}
.BanarSection .intro2 p {
  font-style: normal;
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 22px;
  color: var(--color2);
}

.BanarSection .profile {
  display: flex;
  justify-content: center;
  z-index: 1;
}
.BanarSection .profile .boxContainer {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}
.BanarSection .profile .boxContainer img {
  width: 80px;
}
.BanarSection .profile .box {
  background: var(--color4);
  border-radius: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 0px 8px black;
}
.BanarSection .profile .box1,
.BanarSection .profile .box3 {
  width: 100px;
  height: 100px;
}
.BanarSection .profile .box2 {
  width: 110px;
  height: 110px;
  margin-left: 80px;
  margin-bottom: 50px;
}
.BanarSection .row {
  margin: 0 14%;
}
.connectWithme p.para {
  margin: 20px 0 5px 0;
}
.connectWithme .socialmedia i {
  font-size: 2.5rem;
  margin-right: 15px;
  color: var(--color2);
}
.connectWithme .socialmedia i:hover {
  cursor: pointer;
  transform: scale(1.2);
  transition: 0.3s;
}
@media screen and (max-width: 770px) {
  .BanarSection::after {
    display: none;
  }
  .BanarSection .intro .intro2 .iam {
    background-color: var(--color1);
  }
  .BanarSection .intro .intro2 .iam::after {
    background-color: var(--color1);
  }
  .BanarSection .btnBox .btn1{
    background-color: var(--color1);
  }
}
@media screen and (max-width: 500px) {
  .BanarSection .profile .boxContainer {
    display: none;
  }
  /* .BanarSection::after {
    display: none;
  } */
  .profileimage {
    width: 80%;
  }
  .BanarSection .row {
    margin: 0 10%;
  }
  .BanarSection .intro {
    justify-content: center;
  }
}
@media screen and (max-width: 1000px) {
  .BanarSection .profile .box {
    display: none;
  }
}
